<template>
	<view class="">
		<load ref="passwLoad"></load>
		<popUp ref="passwPopUp"></popUp> 
		<image class="bgc-picture1" src="../../static/fristPage/HomePage.png" mode="widthFix"></image>
		<view class="" style="background-color: #2b95d0; position: fixed; bottom: 0; z-index: -2; height: 100vh; width: 750rpx;"></view>		
		<!-- 输入邮箱后输入密码登录 --> 
		<view class="" style="height: 57.3vh;"></view>
		<view  class="e-view2 bgc_FFF" style="height: 494rpx;">
			<view class="" style="height: 59rpx;"></view>
			<view class="ta-c f49rpx fw800 color-H0f649c lsp-5" style="">Enter Password</view>
			<view class="b-r-15 df" 
				style="margin: 41rpx 45.5rpx 34rpx; height: 90rpx; box-shadow: 0 0 2rpx 2rpx #bac6ce; position: relative;"> 
				<view class="">
					<image src="../../static/fristPage/suo_L.png" mode=""
					style="width: 23rpx; height: 26rpx; margin: 32rpx 27rpx 32rpx 42rpx;"></image> 	
				</view> 
				<view class="" style="">
					<image @click="switchDisplay" :src="eyeFlag ? srcEye1 : srcEye2" mode="widthFix" 
					style="width: 29rpx; height: 26rpx; position: absolute; top: 35rpx; right: 47rpx;"></image>
				</view>
				<view class="df aic">
					<input :password="eyeFlag"  placeholder="Password" placeholder-class="input1" 
					class="fw800 f24rpx" cursor-spacing=150  v-model.trim="inputPassword"
					style="caret-color: #ee5b30; color: #007AFF;" />  
				</view>
			</view> 
			<view @click="Login" class="bgc-o-r ta-c f34rpx FFF fw800"
			hover-class="hover_a24" hover-start-time=0 hover-stay-time=150
			style="margin: 0 41.5rpx; height: 84rpx; border-radius: 42rpx; line-height: 84rpx;">Login</view> 
			<view class="df-jcc-aic">
				<view @click="goForgotPassword"  class="ta-c f33rpx fw800 color-H0f649c lsp-5"
					hover-class="hover" hover-start-time=0 hover-stay-time=150
					style="margin: 33rpx  0 0; border-bottom: 3rpx solid #0f649c;">
					Forgot Password?</view> 
			</view>
		</view>
		<!-- 输入邮箱后输入密码登录结束 -->  
		<view class="" v-if="noConCOd">
			<view class="showmodel" style="width: 100%;">
				<view class="" style="height: 280rpx;"></view>
				<view class="bgc_FFF" style="margin: 0 78rpx 0; height: 750rpx; border-radius: 51rpx; position: relative;">
					<view class="" @click="backPoupo" style="position: absolute; top: 18rpx; right: 18rpx;">
						<image src="../../static/fristPage/qx.png" mode="" style="width: 79rpx; height: 79rpx;"></image>
					</view>
					<view class="" style="height: 49rpx;"></view>
					<view class="df-jcc-aic">
						<view class="">
							<image src="../../static/fristPage/Head3.png" mode="" style="width: 166rpx; height: 164rpx;"></image>
						</view>
					</view>
					<view class="ta-c f49rpx color-H0f649c fw800" style="margin: 34rpx 0 20rpx;">
						Oops!
					</view>
					<view class="ta-c f33rpx color-H0f649c" style="margin: 0 50rpx 30rpx;">
						Your country is missing from your profile. 
						Please select the country you live in!"
					</view>
					<view class="" style="position: relative; height: 90rpx">
						<view class="b-r-15" 
						:style="ifCCC?'height: 270rpx': 'height: 90rpx' "
						style="margin: 0 45rpx; width: 506rpx; box-shadow: 0 0 2rpx 2rpx #97aab6; position: absolute; top: 0; left: 0;">
							<view class="df" style="height: 90rpx;" @click.stop="onIcc">
								<view class="df-jcc-aic" style="margin: 0 39rpx 0 52rpx;">
									<image src="../../static/fristPage/wz.png" mode="widthFix" style="width: 15rpx;"></image>
								</view>
								<input class="f28rpx" disabled="true" style="line-height: 90rpx; color: #999b9d; width: 300rpx; height: 100%;" v-model="setCity" />
								<!-- <view class="f28rpx" style="line-height: 90rpx; color: #999b9d;">{{setCity}}</view> -->
								<view class="df-jcc-aic" style="margin-left: 25rpx;"> 
									<image src="../../static/fristPage/Down.png" mode="widthFix" style="width: 29rpx;"></image>
								</view>
							</view>
							<view class="" style="" v-show="ifCCC">
								<view @click="onCanada" class="" style="height: 90rpx; background-color: #f7b9a6; padding-left: 106rpx;">
									<view class="f28rpx" style="line-height: 90rpx; color: #999b9d;">Canada</view>
								</view>
								<view @click="onAmerica" class="bgc_FFF" style="height: 90rpx; padding-left: 106rpx; border-bottom-right-radius: 15rpx; border-bottom-left-radius: 15rpx;">
									<view class="f28rpx" style="line-height: 90rpx; color: #999b9d;">America/USA</view>
								</view>
							</view>
						</view>
					</view>
					<view @click="getContinuew" class="ta-c FFF f33rpx bgc-o-r" 
					style="margin: 35rpx 27rpx 0; height: 84rpx; line-height: 84rpx; border-radius: 42rpx;">
						Continue
					</view>
				</view>
			</view>
		</view>
	</view>  
</template> 

<script>
	export default{ 
		data(){
			return{
				inputPassword:'',
				eyeFlag:true, 
				noConCOd:false,
				srcEye1:"../../static/fristPage/byL.png", 
				srcEye2:'../../static/fristPage/zhenyanL.png', 
				setCity:'Country',
				ifCCC:false,
				city:''
			}
		},
		methods:{ 
			backPoupo(){
				this.noConCOd = false
			},
			async getContinuew(){
				if(this.city == ''){
					this.$refs.passwPopUp.openPopup('Please select address')
					return false
				}
				console.log(this.city);
				const res = await this.$myRequest({
					url:'/api/user/update',
					method:'POST',
					data:{
						"countryCode":this.city + ''
					}
				}) 
				console.log(res); 
				if(res.data.succeeded){
					uni.switchTab({
						url:"../index/index"
					})
				}
			},
			onIcc(){
				this.ifCCC = !this.ifCCC
			},
			onCanada(){
				this.setCity = 'Canada'
				this.city = 'CA'
				if(this.ifCCC == true){
					this.ifCCC = false
				}
			},
			onAmerica(){ 
				this.setCity = 'America/USA'
				this.city = 'US'
				if(this.ifCCC == true){
					this.ifCCC = false
				}
			},
			// 切换显示密码
			switchDisplay(){
				this.eyeFlag = !this.eyeFlag
			},
			//
			async goForgotPassword(){	
				let email = uni.getStorageSync('Email')
				this.$refs.passwLoad.openLoad()
				console.log(email);
				const res = await this.$myRequest({
					url:'/api/auth/recover' + '?email=' + email,
				}) 
				console.log(res);
				if(res != ''){
					this.$refs.passwLoad.closeLoad()
				}
				uni.redirectTo({
					url:"./forgotPassword"
				})
			},
			//登录
			async Login(){
				let email = uni.getStorageSync('Email')
				var _this = this
				if(_this.inputPassword != ''){
					_this.$refs.passwLoad.openLoad()
					const {data:res} = await this.$myRequest({
						method:'POST',
						url:'/api/auth/login',
						data:{
							"email":email,
							"password":_this.inputPassword
						}
					}) 
					console.log(res);
					if(res != ''){
						_this.$refs.passwLoad.closeLoad()
					}
					if(res.succeeded && res.data.countryCode !=null){
						let token = res.data.token
						uni.setStorageSync('hasActive',res.data.hasActive)
						uni.setStorageSync('token',token)
						uni.setStorageSync('Password',_this.inputPassword)
						console.log('密码正确');
						if(res.data.active && res.data.isBind){
							uni.setStorageSync('isLogin',true)
							uni.setStorageSync('isBind',true)
							//绑定加订阅
							uni.switchTab({
								url:"../index/index"
							})
						}else if(!res.data.active && res.data.isBind){
							//没订阅,已绑定
							uni.setStorageSync('isBind',true)
							uni.setStorageSync('isLogin',true)
							uni.reLaunch({
								url:'../subscription/subscription'+'?ss='+ false
							})
						}else if(!res.data.active && !res.data.isBind){
							uni.setStorageSync('isBind',false)
							// 没订阅没绑定
							uni.reLaunch({
								url:'../subscription/subscription'+'?ss='+false
							})
						}else if(res.data.active && !res.data.isBind){
							uni.setStorageSync('isBind',false)
							//订阅没绑定
							uni.reLaunch({
								url:'../subscription/Bind'
							})
						}

					}else if(res.errno == 304){
						console.log('密码错误');
						uni.redirectTo({
							url:'./WrongPassw'
						})
					}else if(res.succeeded==true && res.data.countryCode ==null){
						let token = res.data.token
						uni.setStorageSync('token',token)
						uni.setStorageSync('Password',_this.inputPassword)
						this.noConCOd = true
						console.log('地址空');
					}else{
						this.$refs.passwPopUp.openPopup(res.errmsg)
					}
					
				}else{ 
					this.$refs.passwPopUp.openPopup('Enter Password')
					return false
				}
				 
			},
		}
	}
</script>

<style scoped>
	.bgc-picture1{
		 z-index:-1; 
		 width:750rpx; 
		 height:100%; 
		 position: fixed;  
		 top:0; 
	}
	.e-view2{
		margin: 0 78.5rpx;
		border-radius: 51rpx;
	}
	.input1{
		font-size: 24rpx;
		/* font-family: myfonts; */
		font-weight: 800;
		color: #91a1aa;
	}
	.showmodel{
		/* z-index: 1; */
		background: rgba(0,0,0,0.5);
		position: fixed;
		left: 0;
		right: 0;
		bottom: -100rpx; 
		top: 0;
	}
</style>
